/* eslint-disable no-restricted-globals */
import * as XLSX from 'xlsx';

// ...resto del codice

self.onmessage = (e) => {
  console.log('Worker ricevuto file:', e.data.file);
  const { file } = e.data;
  const reader = new FileReader();

  reader.onload = (event) => {
    try {
      const data = new Uint8Array(event.target.result);
      const workbook = XLSX.read(data, { type: 'array' });
      const sheet = workbook.Sheets[workbook.SheetNames[0]];
      const rows = XLSX.utils.sheet_to_json(sheet, { header: 1 });
      self.postMessage({ rows });
      console.log('Parsing completato, righe:', rows.length);
    } catch (error) {
      console.error('Errore parsing:', error);
      self.postMessage({ error: 'Errore durante il parsing del file Excel.' });
    }
  };

  reader.onerror = () => {
    self.postMessage({ error: 'Errore nella lettura del file.' });
  };

  reader.readAsArrayBuffer(file);
};